import Component from '../react/Component'

const ReactDOM = {
  render
}

function render(vnode, container) {
  return container.append(_render(vnode))
}

// ËøîÂõûË¶ÅÊ∏≤Êüì
function _render(vnode) {
  // console.log(vnode)
  // vnode‰∏∫Á©∫„ÄÅ‰∏∫undefined„ÄÅ‰∏∫boolean
  if (vnode === null ||
    vnode === undefined ||
    typeof vnode === 'boolean')
    return

  // vnode‰∏∫Êï∞Â≠ó
  if (typeof vnode === 'number') {
    vnode = String(vnode)
  }
  // vnode‰∏∫Â≠óÁ¨¶‰∏≤
  if (typeof vnode === 'string') {
    const textNode = document.createTextNode(vnode)
    return textNode
  }

  // ÁªÑ‰ª∂
  // ÂàõÂª∫ÁªÑ‰ª∂
  // ËÆæÁΩÆÂ±ûÊÄß
  // Ê∏≤ÊüìÁªÑ‰ª∂

  if (typeof vnode.tag === 'function') {
    const comp = createComponent(vnode.tag, vnode.attrs)
    setComponentProps(comp, vnode.attrs)
    return comp.base
  }

  // vnode‰∏∫object
  // ÂàõÂª∫‰∏Ä‰∏™ÂÖÉÁ¥†
  // ÈÅçÂéÜÂä†‰∏äÂ±ûÊÄß
  // appendÂà∞container‰∏ä
  if (typeof vnode === 'object') {
    const {
      tag,
      attrs
    } = vnode
    let dom = document.createElement(tag)
    if (attrs) {
      Object.keys(attrs).map(key => {
        setAttribute(dom, key, attrs[key])
      })
    }

    vnode.children && vnode.children.forEach(child => render(child, dom))

    return dom
  }
}

// ÂàõÂª∫ÁªÑ‰ª∂
function createComponent(comp, props) {
  // Á±ªÁªÑ‰ª∂Áõ¥Êé•ÂàõÂª∫ËøîÂõû
  // ÂáΩÊï∞ÁªÑ‰ª∂Â∞±Ë¶ÅÊûÑÈÄ†ÊàêÁ±ªÁªÑ‰ª∂
  let instance
  if (comp.prototype && comp.prototype.render) {
    instance = new comp(props)
  } else {
    instance = new Component(props)
    instance.constructor = comp
    instance.render = function () {
      return this.constructor(props)
    }
  }

  return instance
}

// ËÆæÁΩÆÁªÑ‰ª∂
function setComponentProps(comp, props) {
  // Â±ûÊÄßÂ§ÑÁêÜ
  // comp.baseÊòØÂê¶Â∑≤ÁªèÂá∫Áé∞
  // Âê¶ÂàôÊòØÂàùÂßãÂåñÊï∞ÊçÆ
  // ÊòØÂàôcomponentWillReceivePropsÂàôÊé•ÂèóÊñ∞ÁöÑprops

  if (!comp.base) {
    if (comp.componentWillMount) {
      comp.componentWillMount()
    }
  } else if (comp.componentWillReceiveProps) {
    comp.componentWillReceiveProps()
  }
  comp.attrs = props
  renderComponent(comp)
}

// Ê∏≤ÊüìÁªÑ‰ª∂
export function renderComponent(comp) {
  console.log('comp', comp)
  const renderer = comp.render()
  let base = _render(renderer)

  if (comp.base && comp.componentWillUpdate) {
    comp.componentWillUpdate()
  }

  if (comp.base) {
    if (comp.componentDidUpdate) {
      comp.componentDidUpdate()
    }
  } else if (comp.componentDidMount) {
    comp.componentDidMount()
  }

  // ËäÇÁÇπÊõøÊç¢
  if (comp.base && comp.base.parentNode) {
    comp.base.parentNode.replaceChild(base, comp.base)
  }

  comp.base = base
}

// ËÆæÁΩÆÂ±ûÊÄß
function setAttribute(dom, key, value) {
  // className => class
  if (key === 'className') {
    key = 'class'
  } else if (/on\w+/.test(key)) {
    // onCLick => click
    key = key.toLowerCase()
    dom[key] = value || ''
  } else if (key === 'style') {
    // style
    // style="color: 'red'"
    // style={{color: 'red'}}
    if (!value && typeof value === 'string') {
      dom.style.cssText = value
    } else if (typeof value === 'object') {
      for (let key in value) {
        if (typeof value[key] === 'number') {
          dom.style[key] = value[key] + 'px'
        } else {
          dom.style[key] = value[key]
        }
      }
    }
  } else {
    // ÂÖ∂‰ªñÁöÑdom[key] = valueÔºå Êõ¥Êñ∞
    if (key in dom) {
      dom[key] = value || ''
    }

    if (value) {
      dom.setAttribute(key, value)
    } else {
      dom.removeAttribute(key)
    }
  }
}

export default ReactDOM