import { renderComponent } from './../react-dom'

class Component {
  constructor(props) {
    this.props = props
    this.state = {}
  }
  setState (newState) {
    // ÂØπË±°Êã∑Ë¥ù
    Object.assign(this.state, newState)
    // ÈáçÊñ∞Ê∏≤ÊüìÁªÑ‰ª∂
    renderComponent(this)
  }
}

export default Component